import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import Page from 'components/LayoutComponents/Page';
import { Card, Row, Col, Table, Input, InputNumber, Select, Form } from 'antd';
import { BtnAddNew, BtnSave, BtnCancel } from 'components/common/button';
import { setListClassNameAgain, schoolYearsTime } from 'dump/data';
import './style.scss';

@Form.create({ name: 'formRegiterStudyAgain' })
class RegisterStudyAgainPage extends Component {
  constructor(props) {
    super(props);
    this.state = {};

    this.columns = [
      {
        title: 'Lớp học lại',
        dataIndex: 'codeClass',
        key: 'codeClass',
      },
    ];

    this.rowSelection = {
      onChange: (selectedRowKeys, selectedRows) => {
        console.log(`selectedRowKeys: ${selectedRowKeys}`, 'selectedRows: ', selectedRows);
      },
      getCheckboxProps: record => ({
        disabled: record.name === 'Disabled User', // Column configuration not to be checked
        name: record.name,
      }),
    };
  }
  handleReset = () => {
    this.props.form.resetFields();
  };
  handleSearch = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      console.log('Received values of form: ', values);
    });
  };
  render() {
    const { getFieldDecorator } = this.props.form;
    const { Option } = Select;
    return (
      <Card title="Đăng ký học lại">
        <Form onSubmit={this.handleSearch}>
          <Row gutter={24}>
            <Col span={8}>
              <Form.Item label="Học kỳ:">
                <InputNumber min={1} max={3} defaultValue={1} style={{ width: '100%' }} />
              </Form.Item>
            </Col>
            <Col span={8}>
              <Form.Item label="Năm học học lại:">
                {getFieldDecorator('schoolYearsTime', {})(
                  <Select>
                    {schoolYearsTime.map(item => (
                      <Option value={item.code}>{item.name}</Option>
                    ))}
                  </Select>
                )}
              </Form.Item>
            </Col>
            <Col span={8}>
              <Form.Item label="Tên lớp đăng ký học lại:">
                <Input placeholder="Nhập tên lớp" />
              </Form.Item>
            </Col>
          </Row>
          <Row gutter={24}>
            <Col span={24}>
              <p>
                Nếu có lớp học lại bạn hãy chọn lớp đăng ký học lại ở bên dưới. Nếu chưa có hãy nhập
                vào ô Tên lớp đăng ký học lại.
              </p>
            </Col>
          </Row>
          <Row gutter={24} style={{ textAlign: 'right' }}>
            <Col span={24}>
              <div className="btn-group">
                <BtnAddNew />
                <BtnSave htmlType="submit" />
                <BtnCancel onClick={() => this.props.history.goBack()} />
              </div>
            </Col>
          </Row>
          <Row gutter={24}>
            <Col span={24}>
              <Table
                bordered
                rowSelection={this.rowSelection}
                dataSource={setListClassNameAgain}
                columns={this.columns}
              />
            </Col>
          </Row>
        </Form>
      </Card>
    );
  }
}

export default withRouter(props => (
  <Page {...props}>
    <RegisterStudyAgainPage {...props} />
  </Page>
));



